.wish-preview-img-container {
    display: grid;
    grid-template: repeat(2, 107px)/2fr 1fr;
    border-radius: 20px;
    gap: 5px;
    margin-bottom: 15px;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    :first-child {
        grid-column: 1;
        grid-row: 1/-1;
        border-bottom-left-radius: 15px;
        border-top-left-radius: 15px;
    }

    :nth-child(2) {
        border-top-right-radius: 15px;
    }

    :nth-child(3) {
        border-bottom-right-radius: 15px
    }
}

.wishList-list {
    display: grid;
    margin-top: 20px;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    justify-content: space-between;
}